.productdisplay {
      display: flex;
      margin: 0px 170px;
}

.productdisplay-left {
      display: flex;
      gap: 20px;

}

.productdisplay-img-list {
      display: flex;
      flex-direction: column;
      gap: 16px;
}

.productdisplay-img-list img {
      height: 110px;
}

.productdisplay-main-img {
      width: 45 0px;
      height: 490px;
}

.productdisplay-right {
      margin: 0px 70px;
      display: flex;
      flex-direction: column;
}

.productdisplay-right h1 {
      color: #3d3d3d;
      font-size: 30px;
      font-weight: 700;
}

.productdisplay-right-star {
      display: flex;
      align-items: center;
      margin-top: 15px;
      gap: 5px;
      color: #1c1c1c;
      font-size: 16px;
}

.productdisplay-right-prices {
      display: flex;
      margin: 10px 0px;
      gap: 30px;
      font-size: 20px;
      font-weight: 700;
}

.productdisplay-right-price-old {
      color: #818181;
      text-decoration: line-through;
}

.productdisplay-right-price-new {
      color: #ff4141;
}

.productdisplay-right-size h1 {
      margin-top: 20px;
      color: #656565;
      font-size: 20px;
      font-weight: 600;

}

.productdisplay-right-sizes {
      display: flex;
      margin: 10px 0px;
      gap: 20px;
}
.productdisplay-right-sizes div{
      padding: 8px 15px;
      background: #fbfbfb;
      border:1px solid #ebebeb;
      border-radius: 3px;
      cursor: pointer;
}
.productdisplay-right button{
      padding: 10px 20px;
      width: 200px;
      font-size: 16px;
      font-weight: 600;
      color: white;
      background: #ff4141e9;
      margin-bottom: 15px;
      border: none;
      outline: none;
      cursor: pointer;
}
.productdisplay-right button:hover{
      background: #ff4141;
}
.productdisplay-right-category{
      margin-top: 5px;
}
.productdisplay-right-category span{
      font-weight: 600;
}