.cartitems{
      margin: 50px 170px;

}
.cartitems hr{
      height: 3px;
      background: #e2e2e2;
      border: 0;
}
.cartitems-formate-main{
      display: grid;
      grid-template-columns: 0.5fr 2fr 1fr 1fr 1fr 1fr;
      align-items: center;
      gap: 70px;
      padding: 15px 0px;
      color: #454545;
      font-size: 18px;
      font-weight: 600;
}
.cartitems-formate{
      font-size: 15px;
      font-weight: 500;

}
.carticon-product-icon{
      height: 62px;
}
.cartitems-remove-icon{
      width: 15px;
      margin: 0px 40px;
      cursor: pointer;
}
.cartitems-quantity{
      width: 44px;
      height: 30px;
      border: 2px solid #ebebeb;
      background: white;

}
.cartitems-down{
      display: flex;
      margin: 100px 0px;
}
.cartitems-total{
      flex:1;
      display: flex;
      flex-direction: column;
      margin-right:200px;
      gap: 30px;
}
.cartitems-total-item{
      display: flex;
      justify-content: space-between;
      padding: 10px 0px;

}
.cartitems-total button{
      width: 262px;
      height: 48px;
      outline: none;
      border: none;
      background: #ff5a5af0;
      color: white;
      font-size: 16px;
      font-weight: 600;
      cursor: pointer;

}
.cartitems-total button:hover{
      background: #ff5a5a;

}
.cartitems-total-item-total{
      font-weight: 600;
}
.cartitems-promocode{
      flex: 1;
      font-size: 16px;
      font-weight: 500;
}
.cartitems-promocode p{
      color: #555;

}
.cartitems-promobox{
      width: 540px;
      margin-top: 15px;
      padding-left: 20px;
      height: 58px;
      background: #eaeaea;
}
.cartitems-promobox input{
      border: none;
      outline: none;
      background: transparent;
      font-size: 16px;
      width: 365px;
      height: 50px;
}
.cartitems-promobox button{
      width: 170px;
      height: 58px;
      font-size: 16px;
      font-weight: 500;
      background: black;
      color: white;
      cursor: pointer;
}